import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  // mode:'history',
  routes: [
    {
      path:'/',
      name:'index',
      component: resolve=>require(['@/components/index'],resolve),
      children:[
        {
          path:'/home',
          name:'home',
          component: resolve=>require(['@/components/home'],resolve)
        },
        {
          path:'/class',
          name:'class',
          component: resolve=>require(['@/components/class'],resolve)
        },
        {
          path:'/classlist',
          name:'classlist',
          component: resolve=>require(['@/components/classlist'],resolve)
        },
        {
          path:'/about',
          name:'about',
          component: resolve=>require(['@/components/about'],resolve)
        },
        {
          path:'/seeview',
          name:'seeview',
          component: resolve=>require(['@/components/seeview'],resolve)
        },
        {
          path:'/resume',
          name:'resume',
          component: resolve=>require(['@/components/resume'],resolve)
        }
      ]
    },
    {
      path:'/server',
      name:'serverHome',
      component: resolve=>require(['@/server/index'],resolve),
      redirect:'/server/home',
      children:[
        {
            path:'/server/home',
            name:'serverStart',
            component: resolve=>require(['@/server/home'],resolve),
        },
        {
          path:'/server/publishair',
          name:'publishair',
          component: resolve=>require(['@/server/publishair'],resolve),
        },
        {
          path:'/server/user',
          name:'user',
          component: resolve=>require(['@/server/user'],resolve),
        },
        {
          path:'/server/label',
          name:'label',
          component: resolve=>require(['@/server/label'],resolve),
        },
        {
          path:'/server/article',
          name:'article',
          component: resolve=>require(['@/server/Article'],resolve),
        }
      ]
    }
  ]
})



